.container_header
        {
          display: flex;
          min-width: 280px;
          max-width: 1220px;
          width: 100%;
          height: 60px;
          justify-content: space-between;
          margin: 0 auto;
          padding-top: 40px;
          .logo
          {
            display: flex;
            a 
            {
              text-decoration: none;
              display: flex;
              cursor: url(../../assets/img/pointer.png), auto;
              h2
              {
              font-family: 'Roboto', sans-serif;
              font-style: normal;
              font-weight: 700;
              font-size: 20px;
              color: #fff;
              cursor: url(../../assets/img/pointer.png), auto;
              }
              svg {
                cursor: url(../../assets/img/pointer.png), auto;
              }
            }

          }
          .container_nav
          {
            margin-right: 150px;
            .nav
            {
              display: flex;
              list-style: none;
              position: relative;
              z-index: 10;
              li //:not(:first-child)
              {
                margin-left: 20px;
                a 
                {
                  color: #fff;
                  font-family: 'Roboto', sans-serif;
                  font-style: normal;
                  font-weight: 700;
                  font-size: 16px;
                  line-height: 23px;
                  text-decoration: none;
                  cursor: url(../../assets/img/pointer.png), auto;
                  &:hover
                  {
                    border-bottom: 1px solid #4AFF93;
                    transition: 0.5s;
                  }
                  &:hover span
                  {
                    color: #fff;
                    transition: 0.7s;
                  }
                }
              }
            }
          }
          .iso_reg
          {
            margin-top: 13px;
            margin-right: 7.5%;
            svg 
            {
              cursor: url(../../assets/img/pointer.png), auto;
            }
          }
        }



@media screen and (max-width: 680px) {
  .container_header
  {
    display: flex;
    min-width: 280px;
    max-width: 1220px;
    width: 100%;
    height: 60px;
    justify-content: space-between;
    margin: 0 auto;
    padding-top: 40px;
    .logo
    {
      display: flex;

      align-items: center;
      height: 100%;
      svg {
        width: 40px;
        height: 40px;
      }
    
    }
    .container_nav
    {
      display: flex;
      margin: 0 auto;
      align-items: center;
      height: 100%;
      .nav
      {
        padding: 0;
        li
        {
          margin-left: 20px;
          a 
          {
            font-size: 14px;
          }
        }
      }
    }
    .iso_reg
    {
      width: 35px;
      height: 35px;
      display: flex;
      margin: 0 auto;
      align-items: center;
      height: 100%;
    }
  }
}


