footer
{
  position: absolute;
  width: 100%;
  bottom: 0;
  margin-bottom: 35px;
  .nav_f
  {
    margin-bottom: 80px;
    ul
    {
      list-style: none;
      display: flex;
      justify-content: space-between;
      width: 600px;
      padding: 0;
      margin: 0 auto;
      li
      {
        a
        {
          color: #5F5F5F;
          text-decoration: none;
          font-family: 'Roboto', sans-serif;
          font-style: normal;
          font-weight: 300;
          font-size: 16px;
          cursor: url(../../assets/img/pointer.png), auto;
          &:hover
          {
            color: #4AFF93;
            transition: 0.3s;
          }
        }
      }
    }
  }
  .feedback_f
  {
    ul
    {
      list-style: none;
      display: flex;
      justify-content: space-between;
      width: 600px;
      padding: 0;
      margin: 0 auto;
      li
      {
        a
        {
          color: #5F5F5F;
          text-decoration: none;
          font-family: 'Roboto', sans-serif;
          font-style: normal;
          font-weight: 300;
          font-size: 16px;
          cursor: url(../../assets/img/pointer.png), auto;
          &:hover
          {
            color: #4AFF93;
            transition: 0.3s;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 680px) {
  footer
{
  .nav_f
  {
    margin-bottom: 75px;
    margin-left: 20px;
    margin-right: 20px;
    ul
    {

      list-style: none;
      display: flex;
      justify-content: space-between;
      max-width: 600px;
      width: 100%;
      li{
        //margin: 0 auto;
        margin-top: 25px;
      }
      }
    }
    .feedback_f
    {
      margin-left: 20px;
      margin-right: 20px;
      ul
      {
        list-style: none;
        display: flex;
        justify-content: space-between;
        max-width: 600px;
        width: 100%;
      }
    }
  }
}







@media screen and (max-width: 375px) {
  footer
{
  .nav_f
  {
    margin: 0;
    margin-bottom: 25px;
    ul
    {

      list-style: none;
      display: grid;
      grid-template-columns:  1fr 1fr ;
      max-width: 600px;
      width: 100%;
      li{
        margin: 0 auto;
        margin-top: 25px;
      }
      }
    }
    .feedback_f
    {
      margin: 0;
      ul
      {
        list-style: none;
        display: flex;
        justify-content: center;
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
        li{
          margin: 10px;
        }
      }
    }
  }
}





@media screen and (max-width: 320px) {
  footer
{
  .nav_f
  {
    margin: 0;
    margin-bottom: 10px;
    ul
    {

      list-style: none;
      display: grid;
      grid-template-columns:  1fr 1fr ;
      max-width: 600px;
      width: 100%;
      li{
        margin: 0 auto;
        margin-top: 25px;
      }
      }
    }
    .feedback_f
    {
      margin: 0;
      margin-bottom: -15px;
      ul
      {
        list-style: none;
        display: flex;
        justify-content: center;
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
        li{
          margin: 10px;
        }
      }
    }
  }
}